.contact__container{
    width: 100%;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 150px;
}
.contact_items{
    background-color: #334257;
    width: 35%;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 100px;
}
.email_contact_section, .phone_contact_section{
    width: 100%;
    color:#EEEEEE;
    font-weight: bolder;
    font-size: 2vw;
    cursor: pointer;
    text-decoration: none;
}
.phone_text{
    text-decoration: none;
    color:#EEEEEE;
}
.email_contact_section:hover, .phone_contact_section:hover{
    background-color: #334257;
    width: 100%;
    height: 100px;
    color: #EEEEEE;
    display: flex;
    font-size: 20px;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 100px;
    box-shadow: 10px 20px 30px #548CA8;
}
