body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color:#476072 ;
  font-family: 'Architects Daughter', cursive;
  /* background-color: #916BBF; */
}


.contact__container{
    width: 100%;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 150px;
}
.contact_items{
    background-color: #334257;
    width: 35%;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 100px;
}
.email_contact_section, .phone_contact_section{
    width: 100%;
    color:#EEEEEE;
    font-weight: bolder;
    font-size: 2vw;
    cursor: pointer;
    text-decoration: none;
}
.phone_text{
    text-decoration: none;
    color:#EEEEEE;
}
.email_contact_section:hover, .phone_contact_section:hover{
    background-color: #334257;
    width: 100%;
    height: 100px;
    color: #EEEEEE;
    display: flex;
    font-size: 20px;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 100px;
    box-shadow: 10px 20px 30px #548CA8;
}


.logo_header{
    width: 60px;
    height:60px;
    cursor: pointer;
    border-radius: 150px;
    border: 2px solid white;
}
.logo_footer{
    width: 60px;
    height:60px;
    cursor: pointer;
    border-radius: 150px;
    border: 2px solid white;
}

@media screen 
  and (max-width: 360px) 
  and (max-height: 640px) 
 {

    .logo_header{
        max-width: 20px;
        max-height:20px;
        cursor: pointer;
        border-radius: 150px;
        border: 2px solid white;
    }

}

.form__container{
    background-color:#e1cef1;
    opacity: 0.7;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

}

.form_title{
    color: #7E3ABA;
    font-size: 2.5vw;
}

.form_input{
    margin: 10px;
    border-radius: 30px;
    height:40px;
    width: 40%;
}
.form_textarea{
    margin: 10px;
    border-radius: 10px;
    width:80%;
    height: 40px;
}
::-webkit-input-placeholder{
    text-align: center;
    font-size: 1.2vw;
    font-style: oblique;
    color: #7E3ABA;
}

.button_submit{
    background-color: #7E3ABA;
    width: 30%;
    height: 40px;
    margin: 20px;
    border-radius: 20px;
    cursor: pointer;
    color: white;
    box-shadow: 10px 7px 7px #7E3ABA;
}
