.header__container{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    background:  -webkit-linear-gradient(90deg, #476072 10%, #334257 70% );
}
.header_logo_container{
  display: flex;
  flex-direction: row;
  justify-content:flex-start;
  margin-top:20px;
  margin-left:30px
}
.profile_section{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.profilePicture{
    width: 35%;
    border-radius: 80px;
    margin-top: 50px;
    cursor:pointer;
    border: 5px solid #548CA8;

}

.name_title{
    font-weight: bolder;
    font-size: 3vw;
    font-family: 'Bonheur Royale', cursive;
    cursor: pointer;
    text-shadow: 1px 1px 2px black;
    color: white;
}
.position_title{
    font-weight: bolder;
    font-size: 2vw;
    font-family: 'Architects Daughter', cursive;
    color: white;
    text-transform: uppercase;
    text-shadow: 1px 1px 2px black;
}

.headerIcon_container{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

}
.iconSection{
    width: 50%;
}
.iconHeader{
    width:8%;
    margin:20px;
    cursor:pointer;
}


.skills__container{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 100px;

}
.skills_header{
    width: 100px;
    height: 100px;
    border-radius: 150px;
    /* background-color: #548CA8; */
    background: -webkit-linear-gradient(90deg, #334257  10%, #476072 50%);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    -webkit-animation: skillsHeader 20s linear 2s infinite alternate backwards running;
            animation: skillsHeader 20s linear 2s infinite alternate backwards running;
    color:white;
    font-weight: bolder;
    font-size: 1.7vw;
    cursor: pointer;

}

@-webkit-keyframes skillsHeader {
    0%{
    right:0px;
    top:0px;
    }
    25%{
    top:300px;
    right:0px
    }
    50%{
        top:0px;
        right:0px
    }
    75%{
        top:300px;
        right:0px;
    }
    100%{
        right:0px;
        top:0px;
    }
  }

@keyframes skillsHeader {
    0%{
    right:0px;
    top:0px;
    }
    25%{
    top:300px;
    right:0px
    }
    50%{
        top:0px;
        right:0px
    }
    75%{
        top:300px;
        right:0px;
    }
    100%{
        right:0px;
        top:0px;
    }
  }

  .skills_button_container{
        width:35%;
        background-color: #EEEEEE;
        opacity: 0.2;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center; 
        margin-right: 100px;
        border-radius: 90px;
        position: relative;
        -webkit-animation: skillsItem 8s ease 2s infinite alternate backwards running;
                animation: skillsItem 8s ease 2s infinite alternate backwards running;
}
.first_list_skills, .second_list_skills, .third_list_skills{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    font-weight: 50px;
    position: relative;
    -webkit-animation: skillsItem 8s ease 2s infinite alternate backwards running;
            animation: skillsItem 8s ease 2s infinite alternate backwards running;
    }
.list_skill{
    margin: 10px;
    color: #EEEEEE;
    list-style-type: none;
    font-size: 1.1vw;
    font-weight: bolder;
    font-style: oblique;
    cursor: pointer;
    position: relative;
    -webkit-animation: skillsItem 8s ease 2s infinite alternate backwards running;
            animation: skillsItem 8s ease 2s infinite alternate backwards running;
}
@-webkit-keyframes skillsItem {
    0%{
        left:0px;
        top:0px
    }
    25%{
        left:100px
        }
        50%{
            left:150px
        }
        75%{
            left:250px;
        }
        100%{
            left:0px;
        }
  }
@keyframes skillsItem {
    0%{
        left:0px;
        top:0px
    }
    25%{
        left:100px
        }
        50%{
            left:150px
        }
        75%{
            left:250px;
        }
        100%{
            left:0px;
        }
  }
.projects_section_container{
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
margin-top:100px;
}
.projects{
    width: 15%;
    height: 80px;
    background-color:#334257;
    border-radius: 20px;
    color: #EEEEEE;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bolder;
    font-size: 2vw;
    /* box-shadow: 5px 10px 20px #548CA8; */
}
.projects:hover{
    width: 20%;
    height: 80px;
    background-color: #334257;
    color: #EEEEEE;
    font-size: 45px;
    box-shadow: 5px 10px 20px #548CA8;
}

.questions_container_row{
    width:50%;
    height: 100px;
    background-color: #334257;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;
    padding: 10px;
    margin-top: 30px;

}


.question{
    width: 100%;
    color: #EEEEEE;
    font-weight: bolder;
    font-size: 1.3vw;
    cursor: pointer;
}
.question_clicked{
    width: 100%;
    height: 30%;
    background-color: #7E3ABA;
    color: #EEEEEE;
    border-radius: 20px;
    font-size: 1vw;
    font-weight: bolder;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.question:hover{
    width: 100%;
    height: 100px;
    background-color: #334257;
    color: #EEEEEE;
    border-radius: 20px;
    font-size: 1.5vw;
    /* font-family: 'Bonheur Royale', cursive; */
    font-weight: bolder;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 10px 20px 30px #548CA8;
}

